import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import List from '@vkontakte/vkui/dist/components/List/List';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import HorizontalScroll from '@vkontakte/vkui/dist/components/HorizontalScroll/HorizontalScroll';
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";
import Home from './Home';
import Favorites from './Favorites';
// import styles from "../styles/App.module.sass";
import styles from "../styles/custom-styles.sass";

const Main = ({id, dataStorage, go, activeTab, tabHome, tabFavorites, count, maxDataLength, lastItem, pay, getPlatform}) => {

    const renderFavorites = dataStorage.map(item => {
        return (
            <Favorites
                key={'itemFavorites' + item.id}
                idItem={item.id}
                photo={item.photo}
                name={item.name}
                year={item.year}
            />
        );
    });

    console.log("lastItem", lastItem);
    return (
        <Panel id={id}>
            <PanelHeader>Случайное кино</PanelHeader>
            <FixedLayout vertical="top">
                <Tabs theme="header" type="buttons">
                    <HorizontalScroll>
                        <TabsItem
                            onClick={tabHome}
                            selected={activeTab === 'home'}
                        >
                            Главная
                        </TabsItem>
                        <TabsItem
                            onClick={tabFavorites}
                            selected={activeTab === 'favorites'}
                        >
                            Мои фильмы
                        </TabsItem>
                    </HorizontalScroll>
                </Tabs>
            </FixedLayout>


            {activeTab === 'home' && maxDataLength === false
                ?
                <Div>
                    <Div>
                        Нажатий: {count}
                    </Div>

                    {lastItem.length !== ''
                        ?
                        <Home
                            key={'itemHome' + lastItem.id}
                            idItem={lastItem.id}
                            photo={lastItem.photo}
                            name={lastItem.name}
                            year={lastItem.year}
                        />
                        :
                        ''
                    }

                    {getPlatform === "iOS"
                        ?
                        <Div className="button">

                            <Button level="commerce"
                                    onClick={pay}
                                    size="xl"
                                    stretched
                                    style={{margin: 40}}>Найти за 1 руб</Button>
                        </Div>
                        :
                        <Div className="button">
                            <Div className="divButton">
                                К сожалению, платежи в данном сервисе не работают на устройствах с системой iOS
                            </Div>
                            <Button level="disabled"
                                    size="xl"
                                    stretched
                                    style={{margin: 40}}>Найти за 1 руб</Button>
                        </Div>
                    }
                </Div>
                :
                <Div>
                    Вы получили все фильмы !
                </Div>

            }

            {activeTab === 'home' && maxDataLength === true
                ?
                <Div>
                    <Div>
                        Вы получили все фильмы !
                    </Div>
                </Div>
                :
                ''

            }


            {activeTab === 'favorites'
                ?
                <Div>
                    {renderFavorites}
                </Div>
                :
                ''
            }

        </Panel>

    );
};


export default Main;


