import React, {useState, useEffect} from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import Main from './panels/Main';
import Home from './panels/Home';
import Favorites from './panels/Favorites';

const App = () => {
        const [activePanel, setActivePanel] = useState('main');
        const [fetchedUser, setUser] = useState(null);
        const [activeTab, setActiveTab] = useState('home');
        const [count, setCount] = useState(0);
        const [dataStorage, setDataStorage] = useState([]);
        const [maxDataLength, setMaxDataLength] = useState(false);
        const [dataFirst, setDataFirst] = useState([]);
        const [getPlatform, setGetPlatform] = useState("");
        const [lastItem, setLastItem] = useState(
            {
                id: 0,
                photo: "https://sun9-17.userapi.com/c854524/v854524232/1ab0cc/YP4_Xic08oY.jpg",
                name: "Чтобы получить случайный фильм, нажмите кнопку ниже"
            });
        const [paymentStatus, setPaymentStatus] = useState(false);


        const data = [
            {
                id: 1,
                photo: "https://st.kp.yandex.net/images/film_big/1188529.jpg",
                name: "Достать ножи Достать ножи Достать ножи Достать ножи",
                year: 2019
            },
            {
                id: 2,
                photo: "https://st.kp.yandex.net/images/film_big/1207545.jpg",
                name: "Курьер",
                year: 2019
            },
            {
                id: 3,
                photo: "https://st.kp.yandex.net/images/film_big/835086.jpg",
                name: "Ford против Ferrari",
                year: 2019
            },
            {
                id: 4,
                photo: "https://st.kp.yandex.net/images/film_big/837737.jpg",
                name: "Сиротский Бруклин",
                year: 2019
            },
            {
                id: 5,
                photo: "https://sun9-64.userapi.com/c204716/v204716471/1c563/J4xlZLC9Yeg.jpg",
                name: "Ванильное небо",
                year: 2001
            },
            {
                id: 6,
                photo: "https://sun9-20.userapi.com/c204724/v204724471/1cf8a/-ubqon4oJF0.jpg",
                name: "Искусственный разум",
                year: 2001
            }
        ];
        const min = 1;
        const arrLength = data.length + 1;
        const differenceData = data.length;


        //const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

        useEffect(() => {
            connect.subscribe(({detail: {type, data}}) => {
                if (type === 'VKWebAppUpdateConfig') {
                    const schemeAttribute = document.createAttribute('scheme');
                    schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
                    document.body.attributes.setNamedItem(schemeAttribute);
                }
            });

            connect.sendPromise('VKWebAppGetClientVersion',
            ).then(res => {
                console.log("ВЕРСИЯ ПРИЛОЖЕНИЯ:", res);
                if(res.platform === "iOS"){
                    setGetPlatform("iOS");
                }
            }).catch(error => {
                console.log(error);
            });



            connect.sendPromise('VKWebAppStorageGet',
                {
                    'keys': ['dataStorage']
                })
                .then((res) => {
                    const re = /&quot;/gi;
                    let str = res.keys[0].value;
                    const newStr = str.replace(re, '"');
                    const psj = JSON.parse(newStr);
                    setDataStorage(psj);

                })
                .catch((error) => {
                    console.log(error);
                });

            // fetchData();
        }, []);


        function getRandomInt(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min)) + min; //Максимум не включается, минимум включается
        }

        function targetList1() {
            console.log(' targetList1 lastItem:', lastItem);
            const randomFirst = getRandomInt(min, arrLength);
            const randomFirstId = data[randomFirst - 1];
            setCount(count + 1);




            if (dataStorage.length <= 0) {
                const findRandomFirst2 = dataStorage.find(
                    item => item.id === randomFirstId.id
                );

                if (findRandomFirst2 === undefined && dataStorage.length !== differenceData) {
                    setId(randomFirstId);
                } else {
                    targetList2();
                    setMaxDataLength(true);
                }
            } else {
                const findRandomFirst3 = dataStorage.find(
                    item => item.id === randomFirstId.id
                );
                if (dataStorage.length !== differenceData) {
                    if (findRandomFirst3 === undefined) {
                        setId(randomFirstId);
                    } else {
                        targetList2();
                    }
                } else {
                    // refreshStorage();
                    setMaxDataLength(true);
                }
            }
        }


        function pay() {
            connect.sendPromise('VKWebAppOpenPayForm', {
                "app_id": 7254171,
                "action": "pay-to-group",
                "params": {
                    "amount": 1,
                    "group_id": 189804850   // 189974647
                }
            }).then(res => {
                if (res.status || res.result.status) {
                    setPaymentStatus(true);
                    console.log('ОПЛАТА ПРОШЛА 1');
                    targetList1();

                    console.log(res.status); // работает
                }
            }).catch(error => {
                console.log(error);
            });
        }


        function targetList2() {
            console.log(' targetList1 lastItem:', lastItem);
            const randomFirst = getRandomInt(min, arrLength);
            const randomFirstId = data[randomFirst - 1];

            const findRandomFirst3 = dataStorage.find(
                item => item.id === randomFirstId.id
            );

            if (dataStorage.length !== differenceData) {
                if (findRandomFirst3 === undefined) {
                    setId(randomFirstId);
                } else {
                    targetList1();
                }
            } else {
                // refreshStorage();
                setMaxDataLength(true);
                console.log(" 0000 maxDataLength:", maxDataLength);
            }
        }


        function setId(randomFirstId) {
            dataStorage.push(randomFirstId);
            setDataStorage(dataStorage);
            const a = dataStorage[dataStorage.length - 1];
            setLastItem(a);

            const stringId = JSON.stringify(dataStorage);
            console.log(' setId dataFirst:', dataFirst);

            connect.sendPromise("VKWebAppStorageSet",
                {
                    "key": "dataStorage",
                    "value": stringId
                })
                .then((res) => {
                    console.log(res);
                })
                .catch((error) => {
                    console.log(error);
                });
        }


        function refreshStorage() {
            connect.sendPromise("VKWebAppStorageSet",
                {
                    "key": "dataStorage",
                    "value": ""
                })
                .then((res) => {
                    console.log(res);
                })
                .catch((error) => {
                    console.log(error);
                });
        }


        function returnLastItem(arr) {
            return arr[arr.length - 1];
        }

        function tabHome() {
            setActiveTab('home');
        }

        function tabFavorites() {
            setActiveTab('favorites');
        }

        const go = e => {
            setActivePanel(e.currentTarget.dataset.to);
        };


        return (
            <View activePanel={activePanel}>
                <Main id='main'
                      fetchedUser={fetchedUser}
                      dataStorage={dataStorage}
                      activeTab={activeTab}
                      tabHome={tabHome}
                      tabFavorites={tabFavorites}
                      targetList1={targetList1}
                      count={count}
                      maxDataLength={maxDataLength}
                      lastItem={lastItem}
                      pay={pay}
                      getPlatform={getPlatform}
                />
                <Home id='home'
                      fetchedUser={fetchedUser}
                      go={go}/>
                <Favorites id='favorites'
                           go={go}/>
            </View>
        );
    }
;

export default App;

